@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  background: #171b26;
}
.btn {
  background: linear-gradient(
    92.23deg,
    #0798f2 21.43%,
    #0798f2 50.63%,
    #f2c879 100%,
    #d9c49c 117.04%
  );
  box-shadow: 0px 4.42184px 107.23px #d9c49c rgba(217, 196, 156, 0.51);
}

.btn:hover {
  background: linear-gradient(
    92.23deg,
    #d9c49c 21.43%,
    #f2c879 50.63%,
    #0798f2 100%,
    #0798f2 117.04%
  );
}

.gradient,
.active {
  background: linear-gradient(
    92.23deg,
    #d9c49c 21.43%,
    #f2c879 50.63%,
    #0798f2 100%,
    #0798f2 117.04%
  );
}

.active {
  color: #fff;
  padding: 1rem;
  border-radius: 100%;
}

@layer base {
  body {
    @apply font-secondary text-lg leading-8 text-white;
  }
  .h2 {
    @apply font-primary text-[32px] mb-6 tracking-[10%] uppercase;
  }
  .h3 {
    @apply font-primary font-semibold text-[26px] mb-6 leading-[46px];
  }
  .btn {
    @apply rounded-full font-primary text-white font-medium;
  }
  .btn-sm {
    @apply h-[48px] px-6 text-sm;
  }
  .btn-lg {
    @apply h-[56px] px-10 text-base;
  }
  .btn-link {
    @apply text-base;
  }
  .text-gradient {
    @apply bg-gradient-to-r text-transparent bg-clip-text from-[#0798F2] to-[#F2C879] font-bold font-primary hover:from-[#F2C879] hover:to-[#0798F2];
  }
  .section {
    @apply py-8 lg:py-24 lg:h-screen flex items-center;
  }
}
